/*************************************/
/******** Observable Notebooks *******/
/*************************************/
.observable-container {
  width: 65%;
  margin: auto;
  text-align: left;
  margin-bottom: 25px;
  font-size: 18px;
}
.observable-container button {
  font-size: 18px;
  margin-left: 10px;
  background-color: var(--blueColour);
  border: none;
  padding: 10px 15px;
  color: var(--lightColour);
  box-sizing: border-box;
  cursor: pointer;
  transition: background-color 0.5s ease;
}
.observable-container button:hover, .observable-container button:active {
  background-color: #286380;
}


/*************************************/
/**********  Portfolio Grid **********/
/*************************************/
.portfolio-grid {
  display: grid;
  width: 65%;
  margin: auto;
  text-align: center;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 5px;
  gap: 5px;
  margin-bottom: 80px;
}
.portfolio-grid-entry-wrapper {
  width: 100%;
  /* background-color: white; */
  -webkit-align-self: center;
          align-self: center;
  justify-self: center;
}

.portfolio-grid-entry {
  width: 100%;
  height: 280px;
  position: relative;
  background-position: center;
  background-repeat: no-repeat; 
  background-size: cover; 
}


/*************************************/
/*********** Project Details *********/
/*************************************/

.popup-wrapper {
  position: fixed;
  top: 50vh;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 100;
  width: 100%;
  max-width: 600px;
}


/*************************************/
/********  Portfolio Grid Btn  *******/
/*************************************/

.portfolio-grid-btn {
  height: 100%;
  width: 100%;
  text-align: center;
  opacity: 0;
  transition: background-color 2s; 
  outline: none;
  border: none;
  font-size: 20px;
  color: white;
  /* currently these break firefox
  -webkit-filter: url(#static-noise);
  filter: url(#static-noise);
  */
}
.portfolio-grid-btn:hover {
  opacity: 0.8;
}
.full-project:hover {
  background-color: var(--purpleColour);
}
.individual-viz:hover {
  background-color: var(--blueColour);
}

.portfolio-grid-project-title {
  position: absolute;
  bottom: 68%;
  left: 50%; 
  -webkit-transform: translate(-50%,0); 
          transform: translate(-50%,0);
  width: 100%;
  padding: 5px;
  text-shadow: 1px 1px 0px rgba(184, 184, 184, 0.4);
  mix-blend-mode: screen;
  cursor: default;
}

.border-button {
  text-transform: uppercase;
  outline: none;
  padding: 7% 14%;
  cursor: pointer;
  box-shadow: 0px 0px 0px 3px var(--lightGreyColour);
  transition: box-shadow 0.6s linear;
  margin: 0.5em;
  transition: background-color box-shadow font-weight 1s; 
}
.border-button:hover { 
  background-color: white;
  opacity: 1 !important;
}
.individual-viz-btn {
  border: 3px solid var(--blueColour);
}
.individual-viz-btn:hover {
  color: var(--blueColour);
  box-shadow: 0px 0px 0px 10px var(--blueColour); 
}
.full-project-btn {
  border: 3px solid var(--purpleColour);
}
.full-project-btn:hover {
  color: var(--purpleColour);
  box-shadow: 0px 0px 0px 10px var(--purpleColour); 
}


