:root {
  --writing-dark-colour: #1e1c1f;
  --writing-third-accent-colour: #48444b;
}

/* container for the whole page */
#writing__page-container {
  max-width: var(--maxWidthSections);
  margin: auto;
  position: relative;
  text-align: center;
  color: var(--textMediumColour);
  padding-top: 50px;
  /* background-color: var(--writing-dark-colour); */
  overflow: hidden; /* TODO: this is a hack; should check why there is an overflow at below 600px*/
  min-height: 100vh;
}

/***********************************************/
/************* Content Container ***************/
/***********************************************/
#writing__content-container {
  width: 77%;
  color: var(--lightGreyColour);
  margin: auto;
  margin-bottom: 50px;
  position: relative;
}


/*************************************************/
/********* Title and Explanation Text ************/
/*************************************************/
#writing__title-text {
  font-family: 'Della Respira', serif;
  text-shadow: 4px 4px 0px rgba(78, 27, 52, 0.3);
  mix-blend-mode: screen;
  cursor: default;
}
.writing__explanation {
  text-align: left;
  line-height: 2;
  font-size: 18px;
}
.writing__explanation span {
  color: var(--textLightcolour);
  padding: 3px;
  cursor: default;
}
.writing__explanation span.resource {
  background-color: var(--purpleColour);
}
.writing__explanation span.example {
  background-color: var(--blueColour);
}
.writing__explanation span.tutorial {
  background-color: #48444b;
  background-color: var(--writing-third-accent-colour);
}


/*************************************************/
/************ Blog Preview Posts *****************/
/*************************************************/
/* container for all the posts */
#writing__post-preview-container {
  margin-top: 70px;
  text-align: left;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
/* container for each individual post*/
.blog-preview-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin-bottom: 50px;
}
/* img for each individual post */
.blog-preview-img {
  min-height: 300px;
  background-color: grey;
  -webkit-flex: 2 1;
          flex: 2 1;
  background-position: center;
  background-repeat: no-repeat; 
  background-size: cover; 
  cursor: pointer;
  transition: 0.7s ease;
}
.blog-preview-img:active {
  -webkit-transform: scale(0.97);
          transform: scale(0.97);
}
/* content for each individual post */
.blog-preview-content {
  background-color: var(--lightGreyColour);
  margin-left: 20px;
  padding: 20px 30px;
  -webkit-flex: 4 1;
          flex: 4 1;
  border-right: 15px solid white; /* colour is just a placeholder */
  overflow: auto;
  transition: 0.7s ease;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: center;
          justify-content: center;
}
.blog-preview-title {
  font-weight: bold;
  font-family: 'Della Respira', serif;
  color: var(--textDarkColour);
  cursor: pointer; /* For Safari 3.0 to 6.0 */
  transition: background-color 1s; /* For modern browsers */
}
.blog-preview-title:hover {
  background-color: #1e1c1f;
  background-color: var(--writing-dark-colour); /* colour is just a placeholder */
  color: var(--textLightcolour);
}
.blog-preview-date {
  color: #666666;
  margin-top: 3px;
  cursor: default;
}
.blog-preview-summary {
  margin-top: 10px;
  color: var(--textDarkColour);
  line-height: 1.5;
  font-size: 16px;
  font-weight: 300;
}
.blog-preview-btn {
  outline: none;
  border: none;
  color: var(--textLightcolour);
  font-size: 16px;
  padding: 10px 25px;
  text-transform: uppercase;
  margin-top: 15px;
  cursor: pointer;
}
.blog-preview-btn:hover {
  opacity: 0.8;
}
.blog-preview-btn:active {
  -webkit-transform: scale(0.97);
          transform: scale(0.97);
}

/************************************************/
/******************** Media  ********************/
/************************************************/

@media(max-width: 1300px) {
  .blog-preview-img, .blog-preview-content {
    height: 380px;
  }
}
@media(max-width: 1100px) {
  #writing__page-container {
    padding-top: 30px;
  }
  .blog-preview-container {
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-bottom: 60px;
  }
  .blog-preview-img {
    -webkit-flex: 1 1;
            flex: 1 1;
    min-height: 250px;
  }
  .blog-preview-content {
    margin-left: 0;
    padding: 20px 30px;
    -webkit-flex: 1 1;
            flex: 1 1;
  }
}
@media(max-width: 600px) {
  #writing__content-container {
    width: 100%;
  }
  /* 
    need to manually add margin on both sides if the width of the container is 100%;
    if the width of the whole container is 90%, delete this rule
  */
  #writing__title-container, .writing__explanation{
    margin-left: 5%;
    margin-right: 5%;
  }
  #writing__post-preview-container {
    margin-top: 50px;
  }
  .blog-preview-container {
    margin-bottom: 80px;
  }
  /* content for each individual post */
  .blog-preview-content {
    margin-left: 0px;
    padding: 20px 25px;
    }
}


:root {
  --margin-bottom-between-sections: 60px; /* margin bottom between different sections */
  --margin-bottom-inside-sections: 30px; /* margin bottom inside a section e.g. after the title */
  --max-wdith-single-col: 900px;
}


/******************************************/
/************ Outer Containers ************/
/******************************************/
/* wrapper for the whole thing - need this because of the background colour */
.post-template__wrapper {
  background-color: var(--lightGreyColour);
}
/* wrapper across all the actual content - this has a max width */
.post-template__container {
  width: 100%;
  max-width: var(--maxWidthSections);
  margin: auto;
  text-align: center;
  position: relative;
  margin-top: 0px;
  margin-bottom: 0px;
  padding: 100px 50px;
  color: var(--textDarkColour);
}


/******************************************/
/****** General Attributes of Posts *******/
/******************************************/
/* Title, subtitle, date, etc. - attributes of all the posts*/
.post-template__title-text {
  font-family: 'Della Respira', serif;
  margin-bottom: 30px;
  margin-bottom: var(--margin-bottom-inside-sections);
  font-weight: 600;
}
/* date of post */
.post-template__date {
  color: var(--textMediumColour);
  margin-bottom: 60px;
  margin-bottom: var(--margin-bottom-between-sections);
}
/* highlighted text */
.post__highlighted-text {
  background-color: rgba(78, 27, 52, 0.8);
  color: var(--textLightcolour);
  padding: 0 3px;
}
/* emphasised text in bold */
.post__emph-text {
  font-weight: bold;
  color: var(--purpleColour);
}
/* general link to site or section */
.post__link {
  border-bottom: 2px dashed var(--purpleColour);
  color: var(--purpleColour);
}
.post__link:hover {
  background-color: rgba(78, 27, 52, 0.8);
  color: var(--textLightcolour);
}
/* lists outside the table of contents */
.post__list-in-body {
  text-align: left;
  padding-left: 50px;
  list-style: none; /* Remove default bullets */
}
.post__list-in-body li::before {
  content: "\273D";  /* Add content: \2022 is the CSS Code/unicode for a bullet */
  color: var(--purpleColour);
  font-weight: bold; /* If you want it to be bold */
  display: inline-block; /* Needed to add space between the bullet and the text */
  width: 1em; /* Also needed for space (tweak if needed) */
  height: 1em;
  margin-left: -1em; /* Also needed for space (tweak if needed) */
  font-size: 16px;
}

.code {
  padding: 0px 5px;
  font-size: 16px;
  background-color: rgba(27, 58, 75, 0.5);
  color: white;
  font-family: monospace;
}


/******************************************/
/************ Table of Contents ***********/
/******************************************/
/* table of contents */
.post-template__table-of-contents {
  max-width: 900px;
  max-width: var(--max-wdith-single-col);
  margin: auto;
  text-align: left;
  margin-bottom: 60px;
  margin-bottom: var(--margin-bottom-between-sections);
}
.post-template__table-of-contents-title {
  margin-bottom: 30px;
  margin-bottom: var(--margin-bottom-inside-sections);
}
.post-template__table-of-contents-main-list {
  line-height: 2.6;
}
.post-template__table-of-contents ul {
  text-align: left;
  padding-left: 50px;
  font-size: 18px;
}
.post-template__table-of-contents ul li::before {
  color: var(--purpleColour);
  font-weight: bold; /* If you want it to be bold */
  display: inline-block; /* Needed to add space between the bullet and the text */
  width: 2em; /* Also needed for space (tweak if needed) */
  margin-left: -1em; /* Also needed for space (tweak if needed) */
}

.post__toc-link {
  border-bottom: 2px solid var(--purpleColour);
  color: var(--purpleColour);
}
.post__toc-link:hover {
  background-color: rgba(78, 27, 52, 0.8);
  color: var(--textLightcolour);
}



/******************************************/
/**************** Sections ****************/
/******************************************/
/* for when we only have 1 col of text and don't want it to stretch over the whole screen */
/* use wrapper to postion title , body etc. in the middle and align left */
.post-template__single-col-wrapper {
  max-width: 900px;
  max-width: var(--max-wdith-single-col);
  margin: auto;
  text-align: left;
}
/* section title */
.post-template__title-container {
  margin: auto;
  position: relative;
  color: var(--textDarkColour);
  border-left: 10px solid var(--purpleColour);
  opacity: 1;
  z-index: 2;
  margin-bottom: 30px;
  margin-bottom: var(--margin-bottom-inside-sections);
}
.post-template__section-title-text {
  font-weight: 300;
  margin: auto;
  text-align: left;
  padding: 5px 10px;
  font-size: 36px;
  font-family: 'Della Respira', serif;
  background-color: rgba(255, 255, 255, 0); 
}
/* section subtitle */
.post-template__section-subtitle {
  padding: 0;
  margin-bottom: 30px;
  margin-bottom: var(--margin-bottom-inside-sections);
  font-family: 'Della Respira', serif;
  font-weight: 400;
  font-size: 26px;
}
/* one paragraph */
.post-template__single-col-para {
  margin-bottom: 60px;
  margin-bottom: var(--margin-bottom-between-sections);
  line-height: 1.7;
  text-align: left;
}

/* for when we have two columns e.g. with code and text or graph and text */
.post-template__two-col-wrapper {
}
.post-template__two-col-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  line-height: 1.7;
  margin-bottom: 60px;
  margin-bottom: var(--margin-bottom-between-sections);
}
.post-template__two-col-container div {
  text-align: left;
}
.post-template__two-col-left {
  margin-right: 20px;
  -webkit-flex: 1 1;
          flex: 1 1;
}
.post-template__two-col-right {
  margin-left: 20px;
  -webkit-flex: 1 1;
          flex: 1 1;
}


/******************************************/
/****************** Media *****************/
/******************************************/
@media (max-width: 1200px) {
  .post-template__two-col-container {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    }
  .post-template__two-col-left {
    margin-right: 0px;
  }
  .post-template__two-col-right {
    margin-top: 30px;
    margin-left: 0px;
  }
}
@media (max-width: 900px) {
  .post-template__container {
    padding: 100px 60px;
  }
}
@media (max-width: 700px) {
  .post-template__container {
    padding: 100px 60px;
  }
  .post-template__table-of-contents ul {
    padding-left: 25px;
  }
  .post-template__two-col-left {
    width: 100%;
    max-width: 550px;
  }
  .post-template__two-col-right {
  }
}
@media (max-width: 550px) {
  .post-template__container {
    padding: 40px 20px;
  }
  .post-template__two-col-left {
    max-width: 350px;
  }
}
@media (max-width: 350px) {
  .post-template__container {
    padding: 40px 0px;
  }
  .post-template__two-col-left {
    max-width: 300px;
  }
}


